.about-container {
  font-family: 'Alegreya Sans', sans-serif;
  text-align: center;
  color: white;
  max-width: 820px;
  min-width: 375px;
  margin: auto;
}

.about-container h1 {
    font-weight: 100;
    font-size: 45px;
    font-family: 'Stardos Stencil', cursive;
}

.about-container h2 {
  margin-top: 40px;
  font-weight: bold;
}

.about-container h3 {
  margin-bottom: 15px;
}

.bio {
  margin: 15px;
  font-family: 'Dosis', sans-serif;
  text-align: left;
}

.about-section {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}

.photo-carousel {
  max-width: 373px;
  height: 373px;
  -o-object-fit: cover;
     object-fit: cover;
}


.slick-list {
  max-width: 373px;
  max-height: 375px;
  border-radius: 10px;
  margin: auto;
}

.slick-slider {
  max-width: 475px;
  margin: auto;
}

.slick-next:before, .slick-prev:before {
    font-size: 40px;
    line-height: 1;
    opacity: .75;
    color: white;
    display: none;
}

.slick-dots li.slick-active button:before {
  color: white;
}

.slick-slider .slick-initialized {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.slick-dots li button:before  {
  color: white;
}

@media only screen and (min-width: 620px) {
  .slick-next:before, .slick-prev:before {
    display: block;
  }
}



