.menu {
  display: -ms-flexbox;
  display: flex;
  font-size: 16px;
  /* justify-content: flex-end; */
}



.menu-bars {
  padding: 25px;
  font-size: 40px;
}

.menu-bars:hover {
  cursor: pointer;
}

#navBar-container {
  z-index: 2;
}

.nav-container {
  position: fixed;
  width: 100%;
  top: 0;
  min-height: 100%;
  background: white;
  opacity: .93;
  font-family: 'Stardos Stencil', cursive;
  font-size: 45px;
  z-index: 100;
  grid-template-rows: 1fr 5fr;
  display: none;
}

.close-nav {
  font-family: sans-serif;
  text-align: right;
}

.close-nav div {
  padding: 25px;
  font-size: 50px;
}

.close-nav div:hover {
  cursor: pointer;
}

.resume-nav {
  font-family: 'Stardos Stencil', cursive;
  font-size: 45px;
  color: black;
  text-decoration: none;
  display: block;
}

.nav-ul {
  /* display: grid; */
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  list-style: none;
  margin-left: 5%;

}

.nav-ul li:hover {
  cursor: pointer;
}

.nav-ul a:hover {
  cursor: pointer;
}

.slideInLeft {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-name: leftSlideIn;
          animation-name: leftSlideIn;
}

.slideOutLeft {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-name: leftSlideOut;
          animation-name: leftSlideOut;
}

@-webkit-keyframes leftSlideIn {
  0% {
    margin-left: -100%;
  }
  100% {
    margin-left: 0%;
  }
}

@keyframes leftSlideIn {
  0% {
    margin-left: -100%;
  }
  100% {
    margin-left: 0%;
  }
}

@-webkit-keyframes leftSlideOut {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: -100%;
  }
}

@keyframes leftSlideOut {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: -100%;
  }
}


.li-selector {
  display: inline;
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;

}


.li-selector:hover {
  font-size: 60px;
}


.li-selector a:hover {
  font-size: 60px;
}

.li-selector a {
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}






